<template>
  <Mobile v-if="mobile" />
  <Desktop v-else />
</template>

<script>
import Mobile from "./Mobile";
import Desktop from "./Desktop";
export default {
  name: "Homepage",
  components: {
    Mobile,
    Desktop,
  },
  data() {
    return {
      mobile: true,
    };
  },
  mounted() {
    this.rscreen();
    window.addEventListener('resize', (e) => {
      this.rscreen()
    });
  },
  methods: {
    rscreen() {
      if (window.innerWidth < 1420) {
        this.mobile = true;
      } else {
        this.mobile = false;
      }
    },
  },
};
</script>