<template>
  <div class="con">
    <div class="top">
      <h1>{{ $store.state.title }}</h1>
    </div>
    <div ref="chat" class="chat">
      <Message
        v-for="(text, index) in texts"
        :key="index"
        :message="text.msg"
        :class="[text.sent ? 'msg-s' : 'msg']"
      />
    </div>
    <div class="input">
      <div class="tt">
        <span ref="emoji" class="emoji"></span>
        <textarea
          v-model="inpt"
          ref="inp"
          class="text"
          placeholder="Type a message"
          @keypress="keyp"
        >
        </textarea>
        <span class="attach material-icons">attach_file</span>
        <button ref="snd" @click="send" class="material-icons">send</button>
      </div>
    </div>
  </div>
</template>

<script>
import io from "socket.io-client";
import autosize from "autosize";
import Message from "./Message";
import { EmojiButton } from "@joeattardi/emoji-button";

export default {
  name: "Main",
  components: {
    Message,
  },
  data() {
    return {
      texts: [],
      socket: {},
      inp: "",
    };
  },
  computed: {
    inpt: {
      get() {
        return this.inp;
      },
      set(value) {
        this.set(value);
      },
    },
  },
  watch: {
    myName() {
      console.log(this.myName);
    },
  },
  beforeCreate() {
    if (process.env.NODE_ENV !== "development") {
      if (location.protocol === "http:") {
        window.location.href = "https://dinochat2.herokuapp.com";
      }
    }
  },
  created() {
    if (process.env.NODE_ENV === "development") {
      this.socket = io("http://localhost:8081", {
        query: {
          name: this.$store.state.name,
          room: this.$store.state.roomcode,
        },
      });
    } else {
      this.socket = io("https://dinochat2.herokuapp.com", {
        query: {
          name: this.$store.state.name,
          room: this.$store.state.roomcode,
        },
      });
    }
  },
  mounted() {
    // sizing
    const text = this.$refs.inp;
    text.style.maxHeight = "125px";
    autosize(text);
    // sockets
    // TODO: use middleware server side
    this.socket.on("connect", (data) => {
      console.log("connected");
      // const name = this.$store.state.name
      // const roomcode = this.$store.state.roomcode
      // console.log(name, roomcode);
      // this.socket.emit('cred', {name: name, room: roomcode})
    });
    this.socket.on("text", (data) => {
      let text = { msg: data, sent: true };
      this.texts.push(text);
    });
    // Emoji
    const picker = new EmojiButton({
      theme: "auto",
      autoHide: false,
    });
    const trigger = this.$refs.emoji;
    picker.on("emoji", (selection) => {
      this.inp += selection.emoji;
    });
    trigger.addEventListener("click", () => picker.togglePicker(trigger));
    window.addEventListener("beforeunload", function (e) {
      e.preventDefault();
      e.returnValue = "";
    });
  },
  updated() {
    this.$nextTick(() => {
      const view = this.$refs.chat;
      view.scrollTop = view.scrollHeight;
    });
  },
  methods: {
    send() {
      if (this.inp && this.inp.trim() !== "") {
        let text = { msg: this.inp, sent: false };
        this.texts.push(text);
        this.socket.emit("msg", {msg: this.inp, room: this.$store.state.roomcode});
        this.$refs.inp.style.height = "50px";
        this.inp = "";
      }
    },
    set(value) {
      this.inp = value;
    },
    keyp(e) {
      if (e.key == "Enter" && !e.shiftKey) {
        e.preventDefault();
        this.send();
      }
      if (e.key == "Enter" && e.shiftKey) {
        e.preventDefault();
        const txtArea = this.$refs.inp;
        txtArea.value += "\r\n";
      }
    },
  },
};
</script>