<template>
  <div>
    <p>{{ message }}</p>
    <span>{{ time }}</span>
  </div>
</template>

<script>
import moment from "moment";
export default {
  name: "Message",
  props: {
    message: {
      type: String,
      required: true,
    },
  },
  data() {
    return {};
  },
  computed: {
    time() {
      return moment().format("HH:mm");
    },
  },
};
</script>

<style lang="scss" scoped>
.div {
  height: 100%;
  width1: 100%;
}
</style>