import { createStore } from 'vuex'

export default createStore({
  state: {
    title: 'Dinochat',
    roomcode: '',
    name: '',
    noroom: false
  },
  mutations: {
    name(state, value) {
      state.name = value
    },
    roomcode(state, value) {
      state.roomcode = value
    },
    setTitle(state, value) {
      if (state.title !== 'Dinochat') {
        state.title = value
      } else {
        return
      }
    },
    noroom(state, value) {
      state.noroom = value
    }
  },
  actions: {
    name({commit}, value) {
      commit('name', value);
    },
    roomcode({commit}, value) {
      commit('roomcode', value);
    },
    setTitle({commit}, value) {
      commit('setTitle', value);
    },
    noroom({commit}, value) {
      commit('noroom', value);
    }
  },
  getters:{
    noroom(state) {
      return state.noroom
    }
  },
  modules: {
  }
})
