import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Chat from '../views/Chat.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/chat',
    name: 'Chat',
    component: Chat,
    beforeEnter: async (to, from, next) => {
      const Store = require('../store/index.js');
      const name = Store.default._state.data.name;
      const room = Store.default._state.data.roomcode;
      const uri = process.env.NODE_ENV == 'development' ? 'http://localhost:8081' : location.origin
      const req = await fetch(`${uri}/api/room?code=${room}`, {
        "method": 'POST'
      });
      const res = await req.json()
      if (res) {
        if (name !== '' && room !== '') {
          next()
        } else {
          router.push({path: '/'})
        }
      } else {
        Store.default.dispatch('noroom', true)
        console.log(Store.default._state.data.noroom);
      }
    }
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
